// left right padding steps 4vw
//Header 8vh
//Footer 4vh
$phone: 320px;
$tablet: 768px;
$computer: 1440px;

//ANIMATIONS
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes zoomin {
  from {
    transform: scale(0.95);
  }
  to {
    transform: scale(1);
  }
}
@keyframes hoverZoomIn {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.05);
  }
}
@keyframes dropmenu {
  from {
    top: 6vh;
  }
  to {
    top: 8vh;
  }
}
.page {
  animation: ease-out fadein 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1440px;
}
.pageHeading {
  animation: ease-out zoomin 0.3s;
  font-size: 1.8em;
}
.dropdown {
  animation: ease-out fadein 0.2s, ease-out dropmenu 0.2s;
}

//GLOBAL
.Main {
  //Centers all the pages
  display: flex;
  flex-direction: column;
  align-items: center;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: #ece9e6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #ffffff,
    #ece9e6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #ffffff,
    #ece9e6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.Header {
  height: 8vh;
  z-index: 10;
  color: white;
  background-color: black;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 0 4vw;
  .stage {
    display: flex;
    justify-content: space-between;
  }
  .right {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .left {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .Link {
    text-decoration: none;
    color: white;
    margin-left: 1.5vw;
    padding: 0%;
  }
  .Link:hover {
    text-decoration: underline;
    transform: scale(1.05);
  }
  .logo {
    height: 7vh;
    position: relative;
    top: 100%;
  }
  .menu,
  .cross {
    height: 4vh;
    display: none;
  }
  .dropdown {
    display: none;
    z-index: 4;
    width: 30vw;
    position: absolute;
    top: 8vh;
    right: 0;
    flex-direction: column;
    align-items: center;
    background-color: rgb(0, 0, 0, 0.7);
    padding: 10px 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

    .Link {
      margin: 12px 0;
    }
  }
}
.Main {
  padding: 2vh 8vw;
  min-height: 84vh;
}
.Footer {
  height: 4vh;
  width: 100%;
  color: white;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.9em;
  p {
    margin: 0;
  }
}
.Home {
  .stage {
    display: flex;
    flex-direction: row;
    margin: 5vh;
    width: 100%;
  }
  .leftStage {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .rightStage {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .gif {
      animation: ease-out zoomin 0.3s;
      width: 100%;
      max-width: 400px;
      border-radius: 35%;
      mask-image: radial-gradient(rgba(0, 0, 0, 1) 60%, rgba(0, 0, 0, 0) 100%);
    }
  }
  #fixedSpace {
    //fixing a space for the text animation
    line-height: 1.5em;
    min-height: 7.5em;
  }
}
.About {
  max-width: 600px;
  text-align: left;
  p {
    width: 100%;
  }
}
.Resume {
  .PDF {
    height: 70vh;
    width: 80vw;
    max-width: inherit;
    overflow: hidden;
    display: flex;
    justify-content: center;
  }
}
.Cards {
  width: 80vw;
  max-width: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  hr {
    width: 100%;
  }
  a {
    color: inherit;
  }
  ul {
    padding-left: 20px;
  }
  .cardWrap {
    margin: 20px;
    width: 100%;
    max-width: 800px;
    padding: 40px 60px;
    // border-radius: 10px;
    // box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border: 2px solid black;
    transition: transform 0.2s;

    background-color: #aeaeae2a;
  }
  .cardWrap:hover {
    transform: scale(1.05);
  }
  .cardWrap .card {
    display: flex;
    flex-direction: row;
    align-items: center;

    .cardLeft {
      width: 80%;
    }
    .cardRight {
      width: 20%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      img {
        max-height: 200px;
        height: 4.5em;
      }
    }
  }
}
.Contact {
  width: 100%;
  max-width: 600px;
  h3 {
    text-align: center;
  }
  strong {
    margin-bottom: 30px;
  }
  .socialMedia {
    width: 80%;
    margin-bottom: 30px;
    height: 40px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    // align-items: center;
    img {
      height: 100%;
      max-height: 50px;
      transition: transform 0.3s;
    }
    img:hover {
      transform: scale(1.5);
    }
  }
  .contactForm {
    width: 90%;
    border: 2px black solid;
    padding: 20px 30px;
  }
  .submitButton {
    background-color: #4caf50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
  }
  .submitButton:hover {
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
      0 17px 50px 0 rgba(0, 0, 0, 0.19);
  }
  .submitButton:active {
    filter: brightness(0.8);
  }
  .cardWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: 0 0 20px 0;
  }
  .cardLeft,
  .cardRight {
    width: 45%;
  }

  textarea {
    width: 100%;
    min-height: 100px;
    resize: vertical;
    font-family: inherit;
    padding: 0.5em;
    margin: 0;
    box-sizing: border-box;
    margin: 10px 0;
  }
  input[type="text"],
  input[type="email"] {
    width: 100%;
    box-sizing: border-box;
    margin: 10px 0;
    padding: 0.5em;
  }

  .submitWrap {
    display: flex;
    min-height: 50px;
    p {
      display: none;
      font-size: 0.8em;
      margin-left: 10px;
      height: 20px;
    }
  }
}

//LESS THAN PHONE
@media (max-width: $phone) {
  body {
    font-size: 0.8em;
  }
}

//PHONE
@media (max-width: $tablet) {
  body {
    font-size: 1em;
  }
  .Header {
    .menu {
      display: block;
    }
    .cross,
    .Links {
      display: none;
    }
  }
  .Home {
    #animateText {
      font-size: 0.95em;
    }
    .stage {
      display: flex;
      flex-direction: column;
      margin: 5vh;
      width: 100%;
    }
    .rightStage {
      width: 100%;
    }
    .leftStage {
      width: 100%;
    }
  }
  .About {
    text-align: center;
  }
  .Cards {
    .cardWrap {
      padding: 20px;
    }
  }
  .Contact {
    .cardWrap {
      flex-direction: column;
      margin: 0;
      .cardLeft,
      .cardRight {
        width: 100%;
      }
    }
  }
}

//TABLET
@media (max-width: $computer) {
  body {
    font-size: 1.1em;
  }
}

//COMPUTER
@media (min-width: $computer) {
  body {
    font-size: 1.2em;
  }
}
